<template>
  <div class="login">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="login-ruleForm">
      <el-form-item label="用户名" prop="username">
        <el-input type="text" v-model="ruleForm.username" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item class="center">
        <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>

  </div>
</template>

<script>
import axios from 'axios';

export default {
  name:'Login',
  data() {
    return {
      ruleForm: {
        username:"",
        password:''
      },
      // 当点击登录时，验证输入框的内容
      rules:{
        username:[
          {required:true,
          message:'用户名不能为空',
          trigger:'blur'
          }
        ],
        password:[
        {required:true,
          message:'密码不能为空',
          trigger:'blur'
          },
        {min:6,max:16,message:'密码长度不符合要求',trigger:'blur'}
        ]
      }

    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        // valid 为 true 验证通过
        if (valid) {
          // alert('submit!');
          
          // console.log(this.ruleForm);
          // console.log(res);
          // let instance = await axios.create({
          //   headers:{
          //     // 告诉服务器发送数据的格式
          //     "Content-Type":"application/x-www-form-urlencoded"
          //   }
          // })
          // let res = await instance.post('http://www.panghu.com/login.php',`username=${this.ruleForm.username}&password=${this.ruleForm.password}`)
          // console.log(res);
          

          
          localStorage.setItem('token',(new Date()).getTime())
          
          this.$message({
            message:'登录成功',
            type:'success',
            duration:1000,
            onClose:() => {
              // 页面跳转
              // this.$route 当前正在访问的路由信息
              // this.$router 路由对象，控制页面跳转
              this.$router.push('/')  // 编程(js)式跳转
            }

          })
          
        } else {
          // 配置弹出提示
          // console.log('error submit!!');
          this.$message({
            message:'登录失败',
            type:'error',
            duration:1000
          })
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

<style lang="scss">
  .login{
    width: 100%;
    height: 100%;
    // 弹性布局
    display: flex;
    // 设置主轴上的对齐方式
    justify-content: center;
    // 设置交叉轴的对齐方式
    align-items: center;
    .login-ruleForm{
      width: 600px;
      .center{
        text-align: center;
      }
    }
  }
</style>